@import url(https://cdn.syncfusion.com/ej2/material.css);
body {
  margin: 0;
  /* font-family: 'Dosis', sans-serif; */
  font-family: 'Poiret One', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #b39809;
  box-sizing: border-box;
}

a {
  color: white;
}

h1 {
  font-size: 4em;
  font-family: 'Limelight', cursive;

}

h2 {
  font-size: 2em;
  font-family: 'Limelight', cursive;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.slide-in-left-enter {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}

.slide-in-left-enter-active {
  -webkit-transform: translateX(0);
          transform: translateX(0);
  opacity: 1;
  transition: all 200ms;
}

.slide-in-left-exit {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
  opacity: 1;
}

.slide-in-left-exit-active {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  opacity: 0;
  transition: all 200ms;
}

.center {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (max-width: 600px) {
  h1 {
    font-size: 3em;
  }   
}
.button {
  font: inherit;
  padding: 0.5rem 1.5rem;
  border: 1px solid white;
  border-radius: 4px;
  background: #b39809;
  color: white;
  cursor: pointer;
  margin-right: 1rem;
  text-decoration: none;
  display: inline-block;
}

.button--active {
  background: #726006;
}

.button:focus {
  outline: none;
}

.button:hover,
.button:active {
  background: #726006;
}

.button--inverse {
  background: transparent;
  color: #ff0055;
}

.button--inverse:hover,
.button--inverse:active {
  color: white;
  background: #ff0055;
}

.button--danger {
  background: #830000;
  border-color: #830000;
}

.button--danger:hover,
.button--danger:active {
  background: #f34343;
  border-color: #f34343;
}

.button:disabled,
.button:hover:disabled,
.button:active:disabled {
  background: #ccc;
  color: #979797;
  border-color: #ccc;
  cursor: not-allowed;
}

.button--small {
  font-size: 0.8rem;
}

.button--big {
  font-size: 1.5rem;
}
.form-control {
  margin: 1rem 0;
}

.form-control label,
.form-control input,
.form-control textarea {
  display: block;
}

.form-control label {
  font-weight: bold;
  margin-bottom: 0.5rem;
  min-height: 28px;
}

.form-control input,
.form-control textarea {
  width: 50%;
  font: inherit;
  border: 1px solid #ccc;
  background: #f8f8f8;
  padding: 0.15rem 0.25rem;
  margin: auto;
}

.form-control input:focus,
.form-control textarea:focus {
  outline: none;
  background: #ebebeb;
  border-color: #510077;
}

.form-control--invalid label,
.form-control--invalid p {
  color: red;
}

.form-control--invalid input,
.form-control--invalid textarea {
  border-color: red;
  background: #ffd1d1;
}
.lds-dual-ring {
  display: inline-block;
  width: 64px;
  height: 64px;
}

.lds-dual-ring:after {
  content: ' ';
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #510077;
  border-color: #510077 transparent #510077 transparent;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
          animation: lds-dual-ring 1.2s linear infinite;
}

.loading-spinner__overlay {
  height: 100%;
  width: 100%;
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
}

@-webkit-keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.75);
  z-index: 10;
}

.modal {
  z-index: 100;
  position: fixed;
  left: 10%;
  width: 80%;
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 8px;
  text-align: center;
}

.modal__header {
  padding: 1rem 0.5rem;
  background: #2a006e;
  color: white;
  display: flex;
  justify-content: space-around;
  position: relative;
}

.modal__header button {
  background: #ff005500;
}

.modal__header span {
  position: absolute;
    cursor: pointer;
    top: 0;
    right: 0;
    font-size: 2em;
    margin: 0.2em;
    color: #ff0055;
}

.modal__header span:hover {
  color: #ff4382;
}

.modal__header h2 {
  margin: 0.5rem;
}

.modal__content {
  padding: 1rem 0.5rem;
}

.modal__footer {
  padding: 1rem 0.5rem;
  display: none;
}

@media (min-width: 768px) {
  .modal {
    left: calc(50% - 20rem);
    width: 40rem;
  }
}

.modal-enter {
  -webkit-transform: translateY(-10rem);
          transform: translateY(-10rem);
  opacity: 0;
}

.modal-enter-active {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  opacity: 1;
  transition: all 200ms;
}

.modal-exit {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  opacity: 1;
}

.modal-exit-active {
  -webkit-transform: translateY(-10rem);
          transform: translateY(-10rem);
  opacity: 0;
  transition: all 200ms;
}

.admin-header {
    display: flex;
    width: 100vw;
    height: 5em;
    position: fixed;
    background-color: black;
    color: wheat;
    justify-content: space-around;
    align-items: center;
}

.admin-header form {
    display: flex;
}

.admin-header form div {
    display: flex;
    justify-content: center;
    align-items: center;
}

.admin-header form div label {
    min-height: 0;
    margin: 0;
}
.admin {
    display: flex;
    min-height: 100vh;
    background-color: rgb(133, 131, 131);
}

.admin-navigation {
    padding-top: 3em;
    padding-right: 3em;
    font-size: 2em;
    border-right: 2px solid black;
    background: url("https://i.pinimg.com/originals/c7/d6/44/c7d6443c3e206f73ff8c105b5e7d1abb.jpg");
}

.admin-navigation li {
    margin: 1em 0;
    cursor: pointer;
}

.admin-navigation li:hover {
    color: black;
}

.admin-content {
    padding-top: 5em;
    padding-left: 2em;
    text-align: center;
}

.admin button {
    margin-top: 0.5em;
    margin-bottom: 1em;
}

.admin select {
    display: block;
    padding: 1em;
    margin: auto;
}

.start, .end {
    margin: 1em 0;
    color: black;
}

.start .form-control,
.end .form-control,
.start .form-control label,
.end .form-control label {
    display: inline;
}

.start .form-control input,
.end .form-control input {
    width: 10%;
    display: inline;
}
.image-upload.center {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.image-upload__preview {
  width: 13rem;
  height: 10rem;
  border: 1px solid #ccc;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: 1rem;
}

.image-upload__preview img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.header {
    min-height: 5em;
    width: 100vw;
    background-color: rgba(2, 2, 44, 0.527);
    display: flex;
    align-items: center;
    text-align: center;
    position: fixed;
    z-index: 1;
    justify-content: space-between;
    font-family: 'Limelight', cursive;

}

.header .logo-link {
    margin-left: 3em;
}

.header .logo {
    height: 3em;
}

.header .logo img {
    height: 100%;
    border-radius: 5px;
}

.header .title {
    display: flex;
    font-size: 1.2em;
    cursor: pointer;
    font-weight: bold;
    align-items: center;
}

.header .title span {
    padding-left: 1em;
    text-shadow: 0 0 0.2em;
    color: #b39809;
}

.header a {
    text-decoration: none;
}

.nav {
    display: flex;
    list-style: none;
    align-items: center;
    justify-content: flex-end;
    margin-right: 5em;
}

.nav .main-navigation {
    padding: 0 0.5em;
}

.nav li {
    margin: 0 0.5em;
    font-size: 1.2em;
    cursor: pointer;
    font-weight: bold;
    
}

.nav .main-navigation::after {
    content: '';
    display: block;
    width: 0;
    height: 2.2px;
    background:  #b39809;
    transition: width .3s;
}

.nav .main-navigation:hover::after {
    width: 100%;
}

.nav li a {
    text-decoration: none;
}

.active {
    border-bottom: 2px solid #b39809;
}

.nav .user-navigation {
    color: white;
}

.nav #auth, .nav #admin {
    margin-left: 1.5em;
    padding-left: 1.5em;
    border-left: 1px solid #b39809;
}

.main-navigation__menu-btn {
    width: 3rem;
    height: 3rem;
    background: transparent;
    border: none;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin-right: 2rem;
    cursor: pointer;
  }
  
  .main-navigation__menu-btn span {
    display: none;
    width: 3rem;
    height: 2.5px;
    background: white;
    
  }

  .navigation {
    display: block;
  }

@media (max-width: 500px) {
    .header .main-navigation__menu-btn span {
      display: block;
    }

    .header .navigation {
        display: none;
      }

    #drawer-hook .nav {
          flex-direction: column;
      }

    #drawer-hook .nav li a {
          color: #b39809;
      }

    #drawer-hook .nav .main-navigation,
    #drawer-hook .nav .user-navigation {
        color: #b39809;
        padding: 1em;
      }

    .header .nav #auth, .nav #admin {
        margin-left: 0.5;
        padding-left: 0;
        border-left: none;
      }

}
.side-drawer {
  position: fixed;
