  top: 0;
  z-index: 100;
  height: 100vh;
  width: 70%;
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
}

.footer {
    background-color: black;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 3em 0;
    font-family: 'Limelight', cursive;
}

.footer-menu {
    text-align: center;
}

.footer-menu ul {
    -webkit-padding-start: 0;
            padding-inline-start: 0;
    margin-right: 0;
}
.footer .logo {
    height: 3em;
    /* padding-left: 3em; */
}

.footer .logo-link {
    margin-left: 3em;
}

.footer .logo img {
    height: 100%;
}

.footer .title {
    display: flex;
    font-size: 1.2em;
    cursor: pointer;
    font-weight: bold;
    align-items: center;
}

.footer .title span {
    padding-left: 1em;
    text-shadow: 0 0 0.2em;
    color: #b39809;
}

.footer a {
    text-decoration: none;
}

@media (max-width: 500px) {
    .footer a {
