    }
}
.store {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
    flex-grow: 1;
}

.store-info {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    height: 30vh;
    text-align: center;
    position: relative;
    overflow: hidden;
    padding: 1em;
    padding-bottom: 3em;
}

.store-info p {
    font-family: 'Poiret One', cursive;
    color: white;
}

.store-info b {
    font-family: 'Roboto', sans-serif;
}

.store h1 {
    padding-top: 1.3em;
    margin: 0;
    text-shadow: 0 0.1em 0.1em black;
}

.store input:focus {
    outline: none;
    background: #ebebeb;
    border-color: #510077;
  }

.store .store-items {
    display: flex;
    padding: 2em;
    justify-content: space-evenly;
    flex-wrap: wrap;
    min-height: 20vh;
}

.store a {
    margin: 3em;
    text-align: center;
    color: #b39809;
    text-decoration: none;
}

.store .item-card {
    margin: 2em 1em;
    height: 45vh;
    transition: 1s;
    text-align: center;
    display: flex;
    flex-direction: column;
    min-width: 25vw;
}

.store .item-card:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.store .item-card__image {
    flex-grow: 1;
    height: 70%;
}

.store .item-card img {
    max-height: 100%;
    max-width: 100%;
    object-fit: cover;
    border-radius: 5px;
    box-shadow: 0 0 0.2em;
}

.store .item-card__info {
    align-items: center;
    height: 30%;
}

.store .item-card__info button {
    margin: 0;
}

.store .item-card__info p {
    width: 100%;
    padding: 0.2em;
    font-size: 1.3em;
    margin: 0.5em 0;
    display: flex;
    justify-content: space-evenly;
    color: white;
}

.store .categories {
    margin: auto;
}

.store .categories ul {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    -webkit-padding-start: 0;
            padding-inline-start: 0;
}

.store .categories ul li {
    margin: 0.5em;
    cursor: pointer;
    font-size: 1.2em;
}



.card {
  position: relative;
    /* margin: 0; */
    box-shadow: 0 2px 8px rgb(0 0 0 / 26%);
    border-radius: 6px;
