.cart {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
    min-height: 80vh;
}

.cart h1 {
    margin: 0;
    padding-top: 1.6em;
    text-shadow: 0 0.1em 0.1em black;
}

.cart .cart-info p {
    font-family: 'Poiret One', cursive;
    color: white;
}

.cart .card {
    margin: auto;
}

.cart .item-card {
    margin: 1em;
    height: 10vh;
    display: flex;
    justify-content: start;
    align-items: center;
    flex-wrap: nowrap;
    color: white;
}

.cart .titles {
