}

.cart .item-card .item-remove {
    width: 10%;
    font-size: 1.3em;
    color: #ff0055;
}

.cart .item-card .item-remove,
.cart .item-card .item-quantity__handler {
    transition: 0.3s;
    cursor: pointer;
}

.cart .item-card .item-remove:hover,
.cart .item-card .item-quantity__handler:hover {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}

.cart button {
    margin: 2em 0;
}

.card .summary {
    border-top: 1px solid #b39809;
    margin: 0;
    padding-top: 1em;
    height: 2em;
}

.summary span {
    color: "#b39809"; 
    font-weight: "bold";
}

.item-card__alert {
    color: red;
    list-style: none;
    display: flex;
    justify-content: end;
}

.cart .item-card .total {
    width: 45%;
}

.bonus {
    display: flex;
    justify-content: center;
}

.cart .bonus-item {
    align-items: flex-start;
}

@media (max-width: 1150px) {
    .cart .item-card .total {
        width: 75%;
    }
}

@media (max-width: 500px) {
    .cart .item-card span {
        padding: 0 0.2em;
        font-size: 0.8em;
    }

    .cart .item-card img {
        max-height: 70%;
    }

    .cart .cart-info p {
        font-size: 0.8em;
    }
}
